a {
  text-decoration: none;
  color: white;
}
.nav-item {
  font-size: 2rem;
  width: 20%;
  display: flex;
  justify-content: space-around;
  align-items: center;
}
.change-location {
  cursor: pointer;
  top: 0%;
  right: 0;
  margin: 15px;
  padding: 8px;
  border-radius: 5px;
  color: white;
  position: fixed;
  width: auto;
  height: auto;
  background: rgba(22, 1, 1, 0.89);
  transition: 0.5s;
  z-index: 7;
}
.change-Location :hover {
  padding: 10px;
  background: rgb(134, 59, 59);
  color: black;
}
.change-Location :active {
  background: yellow;
  color: black;
}
.sidenav {
  top: 0%;
  left: 0;
  right: 0;
  height: 100%;
  width: 0%;
  background: rgba(0, 0, 0, 0.76);
  position: absolute;
  z-index: 3;
  overflow: hidden;
  transition: 0.5s;
}
